const importers = [
  async (path) => {
    if (!/^\.[\\/](?:src[\\/]storybook[\\/]stories(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.tsx)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(24);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:[\\/]src[\\/]storybook[\\/]stories(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.tsx)$/ */
      './src/storybook/stories/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:src[\\/]app(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.(ts|tsx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(10);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:[\\/]src[\\/]app(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.(ts|tsx))$/ */
      './src/app/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await importers[i](path);
    if (moduleExports) {
      return moduleExports;
    }
  }
}